const currency = document.querySelector("#currency");
const locale = document.querySelector("#locale");
const amount = document.querySelector("#amount");
const appendBool = document.querySelector("#append");
const result = document.querySelector("#result");

const updateValue = () => {
  const resultValue = new Intl.NumberFormat(locale.value, {
    style: "currency",
    currency: currency.value
  }).format(amount.value);
  console.log(resultValue);
  result.innerHTML = `${resultValue} ${
    appendBool.value === "yes" ? currency.value : ""
  }`;
};

currency.onchange = () => updateValue();
locale.onchange = () => updateValue();
amount.addEventListener("input", updateValue);
appendBool.onchange = () => updateValue();

updateValue();
